body {
    font-family: "Ubuntu", sans-serif;
    font-size: 13px;
}


.logo {
    width: 140px;
    height: 140px;
}

/* contact icons */

i.icons {
    font-size: 1.6em;
    width: 1.6em;
    text-align: center;
    line-height: 1.6em;
    background: #222222;
    color: #fff;
    border-radius: 0.8em;
    position: relative;
    left: 15px;
}

i.text {
    font-size: 1em;
    text-align: center;
    line-height: 1.1em;
    font-style: normal;
    position: relative;
    top: -5px;
    left: 20px;
}
a.icon-link:link, a.icon-link:active, a.icon-link:visited {
    color: #ffffff;
    text-decoration: none;
}

a > .top5:hover {
    background-color: #ae3910;
}

.left-sidebar {
    /* Firefox */
    /* Chrome, Safari */
    height: calc(100% - 100px);
    /* IE9+ and future browsers */
}

.progress-bar-label {
    position: relative;
    top: -7px;
    text-align: center;
}

div.left-sidebar {
    /* IE10+ */
    /* Mozilla Firefox */
    /* Opera */
    background-image: -o-radial-gradient(left top, ellipse farthest-corner, #DD4814 0%, #F59331 100%);
    /* Webkit (Safari/Chrome 10) */
    background-image: -webkit-gradient(radial, left top, 0, left top, 572, color-stop(0, #DD4814), color-stop(100, #F59331));
    /* Webkit (Chrome 11+) */
    background-image: -webkit-radial-gradient(left top, ellipse farthest-corner, #DD4814 0%, #F59331 100%);
    /* W3C Markup */
    background-image: -webkit-radial-gradient(ellipse farthest-corner at left top, #DD4814 0%, #F59331 100%);
    background-image: -o-radial-gradient(ellipse farthest-corner at left top, #DD4814 0%, #F59331 100%);
    background-image: radial-gradient(ellipse farthest-corner at left top, #DD4814 0%, #F59331 100%);
    color: #ffffff;
    background: -webkit-linear-gradient(67deg, #e0a621, #eb2f09);
    background: -o-linear-gradient(67deg, #e0a621, #eb2f09);
    background: linear-gradient(23deg, #e0a621, #eb2f09);
    background-size: 400% 400%;
    -webkit-animation: 24s ease infinite;
    animation: 24s ease infinite;

@-webkit-keyframes {
    0% {
        background-position: 0% 68%
    }
    50% {
        background-position: 100% 33%
    }
    100% {
        background-position: 0% 68%
    }
}

@keyframes {
    0% {
        background-position: 0% 68%
    }
    50% {
        background-position: 100% 33%
    }
    100% {
        background-position: 0% 68%
    }
}

color: #ffffff;
min-height: 100%;
}


h5{
    color: #222222;
    font-weight: bold;
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
    .pagination {
        position: relative;
        bottom: 0px;
    }

}




